import { Link } from "gatsby"
import PropTypes from "prop-types"
import React from "react"


const LeftMenu = () => (
  <div className="left-menu"> 
    <ul>
      <li><Link className="nav-link" to="/ml101/">Home</Link></li>
      <li><Link className="nav-link" to="/ml101/rating/">Rating</Link></li>
      <li><Link className="nav-link" to="/ml101/homeworks/">Homeworks</Link></li>
      <li><Link className="nav-link" to="/ml101/materials/">Materials</Link></li>
      <li><Link className="nav-link" to="/ml101/projects/">Projects</Link></li>
    </ul>
  </div>
)


export default LeftMenu
